.Persik {
  display: block;
  width: 100%;
  max-width: 240px;
  margin: 20px auto;
}

.main{
  width: 100%;
  height: 200%;
  margin: 20px;
}

.parent {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 0px;
  gap: 12px;

  position: absolute;
  width: 270px;
  height: 130px;
  left: 10%;
  top: 8%;

}

.title{
  width: 193px;
  height: 29px;

  font-family: 'SF Pro Display';
  font-style: normal;
  font-weight: 600;
  font-size: 24px;
  line-height: 29px;
  /* identical to box height */

  margin: 5%;
  color: #000000;


  /* Inside auto layout */

  flex: none;
  order: 0;
  flex-grow: 50;
}

.svg{
  position: absolute;
  width: 301px;
  height: 174px;
  left: 12%;
  right: 12%;
  top: 44%;
}



.button{
  position: absolute;
  height: 54px;
  left: 8.97%;
  right: 8.72%;
  top: 600px;
  margin: 25px;
  bottom: 40px;

}

.button-div{
  position: flex;
  top: 400px;
  bottom: 30px;
}



.discription{
  width: 270px;
  height: 89px;
  margin: 5%;
  font-family: 'SF Pro Display';
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 24px;
  letter-spacing: 0.0025em;

  color: rgba(0, 0, 0, 0.7);


  /* Inside auto layout */

  flex: none;
  order: 1;
  flex-grow: 0;
}
